import React, { Component } from 'react'
import { Div } from 'glamorous'
import EventGroup from './ui/EventGroup'
import { connect } from 'react-redux'
import loadEvents from '../action-creators/eventsReducer/loadEvents'
import PropTypes from 'prop-types'
import { eventsShape, rearrangedRoomsShape } from '../intl/shapes'

class Events extends Component {
  static propTypes = {
    month: PropTypes.number.isRequired,
    date: PropTypes.number.isRequired,
    rearrangedRooms: rearrangedRoomsShape.isRequired,
    events: eventsShape.isRequired,
    loadEvents: PropTypes.func.isRequired,
  }

  gridStyles = {
    width: '100%',
    height: '100%',
    paddingLeft: '15px',
    marginLeft: '25px',
    position: 'relative',
    top: '20px',
    '@media(max-width: 540px)': {
      top: '43px'
    }    
  }

  componentDidMount () {
    this.props.loadEvents()
  }

  render () {
    const { rearrangedRooms, events, date, month, } = this.props
    
    return (
      <Div css={this.gridStyles}>
        {rearrangedRooms.map(rearrangedRoom => ( 
          <EventGroup
            displayedDate={date}
            displayedMonth={month}
            rooms={rearrangedRoom.rooms}
            events={events}
            key={rearrangedRoom.floor}
          />
        ))}
      </Div>
    )
  }
}

const mapStateToProps = state => ({
  month: state.dateReducer.month,
  date: state.dateReducer.date,
  rearrangedRooms: state.mainContentReducer.rearrangedRooms,
  events: state.eventsReducer.events,
})

const mapDispatchToProps = {
  loadEvents,
}

export default connect(mapStateToProps, mapDispatchToProps)(Events)


