import React, { Component } from 'react'
import Paragraph from './Paragraph'
import PropTypes from 'prop-types'

export default class Time extends Component {
  static propTypes = {
    hours: PropTypes.number.isRequired,
    lessThanTwoDigitHours: PropTypes.bool.isRequired
  }

  lineStyles = {
    content: 'a',
    fontSize:' 0px',
    width: '1px',
    display: 'block',
    marginLeft: '18px',
    marginTop: '5px',
    backgroundColor: '#dfe8f4',
    height: 'calc(100vh * 10)',
    position: 'absolute',
  }

  containerStyles = {
    marginRight: '50px',
    position: 'relative',
    '&::after': {
      ...this.lineStyles
    },
    '&:first-child': {
      '&::after': {
        ...this.lineStyles,
        marginLeft: '0px'
      }
    }
  }

  lessThanTwoDigitHours = {
    '&::after': {
      ...this.lineStyles,
      marginLeft: '9px'
    }
  }

  render () {
    const { hours, lessThanTwoDigitHours } = this.props
    const containerStyles = lessThanTwoDigitHours ?
      { ...this.containerStyles, ...this.lessThanTwoDigitHours } :
      this.containerStyles

    return (
      <Paragraph css={containerStyles}>{hours}:00</Paragraph>
    )
  }
} 




