import glamorous from 'glamorous'
import React, { Component } from 'react'

const RoundBtn = glamorous.button({
  width: '25px',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  height: '25px',
  backgroundColor: '#e8ecef',
  borderRadius: '50%',
  border: 'none',
  color: '#8c8d8f',
  cursor: 'pointer',
  outline: 'none',
  transition: '.15s ease-in-out',
  ':hover,:focus': {
    color: '#2a2b2c'
  },
  ':disabled': {
    backgroundColor: '#e1e5e8',
    color: '#c8cbcf'
  },
})

export default class RoundBtnElement extends Component {
  render () {
    return (
      <RoundBtn {...this.props} />
    )
  }
}


