import React, { Component } from 'react'
import { Div } from 'glamorous'
import TimeLine from '../TimeLine'
import Events from '../Events'

export default class TimeTable extends Component {
  state = {
    clicked: false
  }

  innerStyles = {
    height: '100%',
    paddingBottom: '17px',
    boxSizing: 'content-box',
    overflowX: 'scroll',
    overflowY: 'hidden',
    backgroundColor: '#f6f7f9'
  }

  outerStyles = {
    overflow: 'hidden',
    userSelect: 'none',
    cursor: 'grab',
    '@media(max-width: 540px)': {
      width: '50%'
    }
  }

  triggerClicked = () => {
    this.setState(prevState => ({
      clicked: !prevState.clicked
    }))

    this.timeTable.style.cursor = this.state.clicked ? 'grab' : 'grabbing'
  }

  handleScroll = e => {
    e.preventDefault()

    if (this.state.clicked) {
      this.timeTable.scrollLeft = e.nativeEvent.movementX < 0 
      ?
        this.timeTable.scrollLeft + 20 
      :
        this.timeTable.scrollLeft - 20
    }
  }
  
  render() {
    return (
      <Div css={this.outerStyles}>
        <Div 
          css={this.innerStyles}
          innerRef={ref => this.timeTable = ref}
          onMouseMove={this.handleScroll}
          onMouseDown={this.triggerClicked}
          onMouseUp={this.triggerClicked}
        >
          <TimeLine />
          <Events />
        </Div>
      </Div>
    )
  }
}


