import { LOAD_EVENTS } from '../../consts/eventsReducer'
import getLocalStorageItems from '../../db/queries/getLocalStorageItems'

const loadEventsActionCreator = events => ({
  type: LOAD_EVENTS,
  payload: events
})

const loadEvents = () => {
  return dispatch => {
    getLocalStorageItems('events')
    .then(events => {
      dispatch(loadEventsActionCreator(events))
    })
  }
}

export default loadEvents


