import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Div } from 'glamorous'
import Event from './ui/Event'
import { roomShape, eventsShape } from '../intl/shapes'
import triggerModal from '../action-creators/pageReducer/triggerModal'
import setEventForEditing from '../action-creators/eventsReducer/setEventForEditing'
import triggerSelected from '../action-creators/roomsReducer/triggerSelected'
import { TRIGGER_EDIT_MODAL } from '../consts/pageReducer'
import { connect } from 'react-redux';

class EventRow extends Component {
  static propTypes = {
    room: roomShape.isRequired,
    events: eventsShape.isRequired,
    openingHours: PropTypes.string.isRequired,
    closingHours: PropTypes.string.isRequired
  }
  
  getWidth = () => {
    const { openingHours, closingHours } = this.props

    return `${(closingHours - openingHours) * 145}px`
  }

  containerStyles = {
    display: 'block',
    position: 'relative',
    paddingTop: '25px',
    marginBottom: '32px',
    '@media(max-width: 540px)': {
      backgroundColor: 'rgba(255, 255, 255, 0.7)',
      width: this.getWidth(this.props.openingHours, this.props.closingHours),
      height: '35px',
      marginBottom: '3px',
      marginLeft: '-50px',
      paddingLeft: '50px'
    }  
  }

  getEventDate = date => {
    const { startingDate, startingMonth } = date

    return `${startingDate} ${this.props.monthsValue[startingMonth]}`
  }

  getEventTime = time => {
    const { startingHours, startingMinutes, endingHours, endingMinutes } = time

    return `${startingHours}:${startingMinutes}  ${endingHours}:${endingMinutes}`
  }

  getEventOffsetLeft = (startingHours, startingMinutes) => (
    (startingHours - this.props.openingHours) * 145 + Number(startingMinutes / 60 * 145)
  )

  getEventWidth = time => {
    const { startingHours, startingMinutes, endingHours, endingMinutes } = time

    return (endingHours - startingHours) * 145 + ((endingMinutes / 60 * 145) - (startingMinutes / 60 * 145)) 
  }

  openEditModal = event => {
    const { setEventForEditing, triggerModal, triggerSelected } = this.props

    setEventForEditing(event)
    triggerModal(TRIGGER_EDIT_MODAL)
    triggerSelected(event.room.id)
  }

  render() {
    const { room, events } = this.props

    return (
      <Div css={this.containerStyles}>
        {events.map(event => { 
          const offsetLeft = this.getEventOffsetLeft(
            event.time.startingHours,
            event.time.startingMinutes
          )
          
          const width = this.getEventWidth(event.time)
          const date = this.getEventDate(event.date)
          const time = this.getEventTime(event.time)
          
          return <Event
            key={event.id}
            name={event.name}
            roomName={room.name}
            users={event.users}
            openEditModal={() => {
              this.openEditModal(event)
            }}
            offsetLeft={offsetLeft}
            width={width}
            date={date}
            time={time}
          />
        })}
      </Div>
    )
  }
}

const mapStateToProps = state => ({
  openingHours: state.pageReducer.openTime.hours,
  closingHours: state.pageReducer.closeTime.hours,
  monthsValue: state.dateReducer.monthsValue,
})

const mapDispatchToProps = {
  triggerModal,
  setEventForEditing,
  triggerSelected
}

export default connect(mapStateToProps, mapDispatchToProps)(EventRow)


