import React, { Component } from 'react'
import Headline from './Headline'
import Paragraph from './Paragraph'
import PropTypes from 'prop-types'
import { Div } from 'glamorous'
import { injectIntl, intlShape } from 'react-intl'

class RoomName extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    name: PropTypes.string.isRequired,
    disabled: PropTypes.bool,
    maxVisitors: PropTypes.number.isRequired
  }

  containerStyles = {
    paddingLeft: '25px',
    marginTop: '2px',
    '@media(max-width: 540px)': {
      width: '100%',
      height: '60px',
      backgroundColor: 'rgba(255, 255, 255, 0.7)',
      paddingLeft: '15px',
      marginTop: '-10px',
    }
  }

  disabledStyles = { 
    paragraph: {
      color: '#cacdd2'
    },
    headline: {
      color: '#b7bcc2'
    }
  }

  headlineStyles = { 
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    '@media(max-width: 540px)': {
      paddingTop: '8px',
      width: '83%'
    }
  }

  paragraphStyles = { 
    marginTop: '-10px',
  }

  render () {
    const { name, disabled, maxVisitors, intl: { formatMessage } } = this.props
    const visitorsLimit = formatMessage({id: 'roomName.visitorsLimit'}, {maxVisitors})
    let paragraphStyles = this.paragraphStyles
    let headlineStyles = this.headlineStyles

    if (disabled) {
      headlineStyles  =  {...this.disabledStyles.headline, ...this.headlineStyles}
      paragraphStyles = { ...this.paragraphStyles, ...this.disabledStyles.paragraph }
    }

    return (
      <Div css={this.containerStyles}>
        <Headline css={headlineStyles}>{name}</Headline>
        <Paragraph css={paragraphStyles}>
          {visitorsLimit}
        </Paragraph>
      </Div>
    )
  }
}

export default injectIntl(RoomName)


