import React, { Component } from 'react'
import { Div } from 'glamorous'
import RoomName from './RoomName'
import GreyParagraph from './GreyParagraph'
import PropTypes from 'prop-types'
import { injectIntl, intlShape } from 'react-intl'
import { roomsShape } from '../../intl/shapes'

class RoomGroup extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    floor: PropTypes.number.isRequired,
    rooms: roomsShape.isRequired
  }

  containerStyles = {
    marginTop: '27px',
    ':first-child': {
      marginTop: '17px',
    },

    ':last-child': {
      paddingBottom: '20px'
    },

    '@media(max-width: 540px)': {
      width: '100%',
      marginTop: '23px',

      ':last-child': {
        paddingBottom: '0px'
      },
    }
  }
  
  floorNameStyles = {
    marginBottom: '-5px',
    paddingLeft: '25px',
    '@media(max-width: 540px)': {
      paddingLeft: '15px',
      marginBottom: '20px'
    }
  }

  renderRooms = rooms => (
    rooms.map(room => (
      <RoomName
        name={room.name}
        key={room.name}
        maxVisitors={room.maxVisitors}
      />
    ))
  )

  render () {
    const { floor, rooms, intl: { formatMessage } } = this.props
    const floorName = formatMessage({id: 'roomGroup.floorName'}, {floor})
    
    return (
      <Div css={this.containerStyles}>
        <GreyParagraph css={this.floorNameStyles}>{floorName}</GreyParagraph>
        {this.renderRooms(rooms)}
      </Div>
    )
  }
}

export default injectIntl(RoomGroup)


