import React, { Component } from 'react'
import { Div } from 'glamorous'
import Headline from './Headline'
import Paragraph from './Paragraph'
import EditBtn from './EditBtn'
import TooltipParticipators from './TooltipParticipators'
import PropTypes from 'prop-types'
import { usersShape } from '../../intl/shapes'

export default class Tooltip extends Component {
  static propTypes = {
    name: PropTypes.string.isRequired,
    roomName: PropTypes.string.isRequired,
    date: PropTypes.string.isRequired,
    time: PropTypes.string.isRequired,
    users: usersShape.isRequired,
    openEditModal: PropTypes.func.isRequired
  }
  
  containerStyles = {
    minWidth: '360px',
    position: 'absolute',
    zIndex: '99',
    marginTop: '30px',
    display: 'inline-block',
    padding: '20px 70px 2px 15px',
    backgroundColor: '#fff',
    borderRadius: '6px',
    boxShadow: '-1px 2px 40px -1px #a4b0bc',
    '::after': {
      content: ' ',
      display: 'block',
      position: 'absolute',
      top: '-15px',
      left: '47%',
      width: '0', 
      height: '0', 
      borderBottom: '15px solid #fff',
      borderLeft: '10px solid transparent',
      borderRight: '10px solid transparent'
    },
    '@media(max-width:540px)': {
      position: 'fixed',
      left: '5px',
      bottom: '15px',
      width: 'calc(100% - 95px)',
      minWidth: '0px',
      '::after': {
        display: 'none'
      }
    }
  }

  editBtnStyles = {
    position: 'absolute',
    right: '10px',
    top: '10px'
  }

  headlineStyles = {
    margin: '0px',
    overflow: 'hidden',
    textOverflow: 'ellipsis'
  }

  paragraphStyles = {
    marginTop: '5px'
  }

  render () {
    const { name, roomName, date, time, users, openEditModal } = this.props

    return (
      <Div css={this.containerStyles}>
      <EditBtn css={this.editBtnStyles} onClick={openEditModal} />
        <Headline css={this.headlineStyles}>{name}</Headline>
        <Paragraph css={this.paragraphStyles}>{date}, {time}  {roomName}</Paragraph>
        <TooltipParticipators users={users} />
      </Div>
    );
  }
}


