import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Div } from 'glamorous'
import Tooltip from './Tooltip'
import onClickOutside from 'react-onclickoutside'
import { usersShape } from '../../intl/shapes'

class Event extends Component {
  state = {
    tooltipOpened: false
  }

  static propTypes = {
    name: PropTypes.string.isRequired,
    roomName: PropTypes.string.isRequired,
    users: usersShape.isRequired,
    time: PropTypes.string.isRequired,
    date: PropTypes.string.isRequired,
    width: PropTypes.number.isRequired,
    offsetLeft: PropTypes.number.isRequired,
    openEditModal: PropTypes.func.isRequired
  }
  

  containerStyles = {
    position: 'absolute',
    display: 'flex',
    justifyContent: 'center',
    height: '30px',
    transition: 'background-color .15s ease-in-out',
    backgroundColor: '#d5dfe9',
    cursor: 'default',
    '@media(max-width: 540px)': {
      height: '60px',
      marginTop: '-25px',
      backgroundColor: 'rgba(191, 206, 221, 0.5)',
    }
  }

  selectedStyles = {
    backgroundColor: '#99a9b9',
    '@media(max-width: 539px)': {
      backgroundColor: 'rgba(133, 152, 171, 0.5)'
    }
  }

  handleClickOutside = () => {
    this.setState({
      tooltipOpened: false
    })
  }

  triggerTooltip = () => {
    this.setState(prevState => ({
      tooltipOpened: !prevState.tooltipOpened
    }))
  }


  render() {
    const { name, roomName, users, time, date, openEditModal, width, offsetLeft } = this.props
    const { tooltipOpened } = this.state

    let containerStyles = tooltipOpened ? 
    { ...this.containerStyles, ...this.selectedStyles } :
    this.containerStyles

    containerStyles = { ...containerStyles, width: `${width}px`, marginLeft: `${offsetLeft}px` }

    return (
      <Div 
        css={containerStyles} 
        onClick={this.triggerTooltip} 
        innerRef={ref => this.event = ref}
      >
        {tooltipOpened &&
          <Tooltip
            name={name}
            roomName={roomName}
            users={users}
            openEditModal={openEditModal}
            date={date}
            time={time}
            focus={tooltipOpened}
          />
        }
      </Div>
    )
  }
}

export default onClickOutside(Event)


