import React, { Component } from 'react'
import { Div } from 'glamorous'
import RoomGroup from './ui/RoomGroup'
import { connect } from 'react-redux'
import { rearrangedRoomsShape } from '../intl/shapes'

class Rooms extends Component {
  static propTypes = {
    rearrangedRooms: rearrangedRoomsShape.isRequired
  }

  containerStyles = {
    paddingRight: '60px',
    minHeight: 'calc(100vh - 120px)',
    borderTop: '1px solid #f5f6f8',
    height: '100%',
    '@media(max-width: 540px)': {
      paddingRight: '0px',
      backgroundColor: '#f7f8f9',
      borderTop: 'none',
      marginTop: '46px'     
    }
  }

  renderRoomGroups = rearrangedRooms => (
    rearrangedRooms.map(obj => {
      return <RoomGroup 
        rooms={obj.rooms}
        floor={obj.floor}
        key={obj.floor} />
    })
  )

  render () {
    const { rearrangedRooms } = this.props

    return (
      <Div css={this.containerStyles}>
        {this.renderRoomGroups(rearrangedRooms)}
      </Div>
    )
  }
}

const mapStateToProps = state => ({
  rearrangedRooms: state.mainContentReducer.rearrangedRooms
})

export default connect(mapStateToProps)(Rooms)


