import { REARRANGE_ROOMS } from '../../consts/mainContentReducer'

const sortByFloor = rooms => {
  if(rooms.length < 2) {
    return rooms
  }

  const pivotPosition = Math.floor(Math.random() * rooms.length)
  const pivot = rooms[pivotPosition]
  const less = rooms.filter(elem => 
    elem.floor < pivot.floor
  )
  
  const greater = rooms.filter(elem => 
    elem.floor > pivot.floor
  )

  return [ ...sortByFloor(greater), pivot, ...sortByFloor(less)]
}

const rearrangeRooms = rooms => {
  let rearrangedRooms = []

  rooms.forEach(room => {
    const { floor, ...otherKeys } = room 
    const newRoom = otherKeys

    if(rearrangedRooms.length > 0) {
      rearrangedRooms.forEach(rearrangedRoomsElem => {
        const isUnique = rearrangedRooms.every(rearrangedRoomsElem =>
          rearrangedRoomsElem.floor !== room.floor
        )

        if(isUnique) {
          rearrangedRooms.push({ floor: room.floor, rooms: [ newRoom ] })   
        }

        else if(rearrangedRoomsElem.floor === room.floor) {
          rearrangedRoomsElem.rooms.push(newRoom)
        }
      })
    }
          
    else {
      rearrangedRooms.push({ floor: room.floor, rooms: [ newRoom ] })
    }
  })

  rearrangedRooms = sortByFloor(rearrangedRooms)

  return { type: REARRANGE_ROOMS, rearrangedRooms: rearrangedRooms }
}

export default rearrangeRooms


