import { LOAD_ROOMS } from '../../consts/roomsReducer'
import getLocalStorageItems from '../../db/queries/getLocalStorageItems'
import rearrangeRooms from '../mainContentReducer/rearrangeRooms'

const loadRoomsActionCreator = rooms => ({
  type: LOAD_ROOMS,
  payload: rooms
})

const loadRooms = () => {
  return dispatch => {
    getLocalStorageItems('rooms')
    .then(rooms => {
      dispatch(rearrangeRooms(rooms))
      dispatch(loadRoomsActionCreator(rooms))
    })
  }
}

export default loadRooms


