import React, { Component } from 'react'
import { Div, Span, Img } from 'glamorous'
import Paragraph from './Paragraph'
import { injectIntl, intlShape } from 'react-intl'
import { usersShape } from '../../intl/shapes'

class TooltipParticipators extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    users: usersShape.isRequired
  }

  containerStyles = {
    display: 'flex',
    alignItems: 'center',
    marginTop: '-15px'
  }

  imageStyles = {
    width: '25px',
    height: '25px',
    borderRadius: '100%'
  }

  greyTextStyles = {
    color: '#8c8d8f'
  }

  imageContainer = {
    width: '25px',
    height: '25px',
    marginRight: '5px'
  }

  render() {
    const { users, intl: { formatMessage } } = this.props
    const firstUser = users[0]
    const remainingUsersLength = users.length - 1
    const remainingParticipators = formatMessage(
            {id: 'tooltipParticipators.remainingParticipators'}, 
            {remainingUsersLength}
          )
    
    return (
      <Div css={this.containerStyles}>
        <Div css={this.imageContainer}>
          <Img src={firstUser.avatarUrl} alt={firstUser.login} css={this.imageStyles} />
        </Div>
        <Paragraph>
          {firstUser.login}
          &nbsp;
          <Span css={this.greyTextStyles}>
            {remainingParticipators}
          </Span>
        </Paragraph>
      </Div>
    )
  }
}

export default injectIntl(TooltipParticipators)


