import React, { Component } from 'react'
import { Div, Main } from 'glamorous'
import { connect } from 'react-redux'
import DatePicker from './DatePicker'
import TimeTable from './ui/TimeTable'
import Rooms from './Rooms'
import fillTimeStripeArray from '../action-creators/mainContentReducer/fillTimeStripeArray'
import loadRooms from '../action-creators/roomsReducer/loadRooms'
import PropTypes from 'prop-types'

class MainContent extends Component {
  static propTypes = {
    openHours: PropTypes.string.isRequired,
    closeHours: PropTypes.string.isRequired,
    loadRooms: PropTypes.func.isRequired,
    hidden: PropTypes.bool.isRequired,
    fillTimeStripeArray: PropTypes.func.isRequired,
  }

  mainContentStyles = {
    width: '100%',
    display: 'flex',
    '@media(max-width: 540px)': {
      marginTop: '60px'
    }
  }

  leftColumnStyles = {
    display: 'flex',
    flexDirection: 'column',
    '@media(max-width: 540px)': {
      width: '50%'
    }
  }

  hiddenStyles = {
    position: 'absolute',
    clip: 'rect(1px, 1px, 1px, 1px)',
    padding: '0',
    border: '0',
    height: '1px', 
    width: '1px', 
    overflow: 'hidden'
  }

  componentDidMount () {
    const { fillTimeStripeArray, openHours, closeHours, loadRooms } = this.props
    
    loadRooms()
    fillTimeStripeArray(Number(openHours), Number(closeHours))
  }

  render() {
    const mainContentStyles = this.props.hidden ?
      {
        ...this.mainContentStyles,
        ...this.hiddenStyles
      } :
      this.mainContentStyles

    return (
      <Main css={mainContentStyles}>
        <Div css={this.leftColumnStyles}>
          <DatePicker />
          <Rooms />
        </Div>
        <TimeTable />
      </Main>
    )
  }
}

const mapStateToProps = state => ({
  openHours: state.pageReducer.openTime.hours,
  closeHours: state.pageReducer.closeTime.hours,
})

const mapDispatchToProps = {
  fillTimeStripeArray,
  loadRooms
}

export default connect(mapStateToProps, mapDispatchToProps)(MainContent)


