import React, { Component } from 'react'
import { Div } from 'glamorous'
import { timeStripeArrayShape } from '../intl/shapes'
import Time from './ui/Time'
import { connect } from 'react-redux'

class TimeLine extends Component {
  static propTypes = {
    timeStripeArray: timeStripeArrayShape.isRequired,
  }

  containerStyles = {
    display: 'flex',
    height: '46px',
    justifyContent: 'space-between',
    padding: '0 20px',
    backgroundColor: '#fff',
    paddingLeft: '40px'
  }

  renderTimeStripes = (timeStripeArray) => (
    timeStripeArray.map(timeStripe => {
      return <Time 
        hours={timeStripe} 
        key={timeStripe} 
        lessThanTwoDigitHours={timeStripe < 10} 
      />
    })
  )

  render() {
    const { timeStripeArray } = this.props
    const lastInTimeArray = timeStripeArray.slice(-1)[0]
    const containerStyles = { 
      ...this.containerStyles, 
      width: `${lastInTimeArray * 100 - 100}px` // no fucking idea...
   }
    
    return (
      <Div css={containerStyles}>
        {this.renderTimeStripes(timeStripeArray)}
      </Div>
    )
  }
}

const mapStateToProps = state => ({
  timeStripeArray: state.mainContentReducer.timeStripeArray,
})

export default connect(mapStateToProps)(TimeLine)


