import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Div } from 'glamorous'
import EventRow from '../EventRow'
import { roomsShape, eventsShape } from '../../intl/shapes'

export default class EventGroup extends Component {
  static propTypes = {
    rooms: roomsShape.isRequired,
    events: eventsShape.isRequired,
    displayedDate: PropTypes.number.isRequired,
    displayedMonth: PropTypes.number.isRequired
  }

  containerStyles = {
    marginBottom: '68px',
    '@media(max-width: 540px)': {
      marginBottom: '48px'
    }    
  }

  renderEventRows = () => {
    const { rooms, events, displayedDate, displayedMonth } = this.props
    
    return rooms.map(room => {
      const neededEvents = events.filter(event => {
        return event.room.id === room.id &&
        event.date.startingDate === displayedDate &&
        event.date.startingMonth === displayedMonth
      })

      return <EventRow
        room={room}
        events={neededEvents}
        key={room.id}
      />
      })
    }

  render() {
    return (
      <Div css={this.containerStyles}>
        {this.renderEventRows()}
      </Div>
    )
  }
}


