import React, { Component } from 'react'
import FontAwesome from 'react-fontawesome'
import RoundBtn from './RoundBtn'
import PropTypes from 'prop-types'
import { stylesShape } from '../../intl/shapes'

export default class ArrowBtn extends Component {
  static propTypes = {
    onClick: PropTypes.func.isRequired,
    rotation: PropTypes.string.isRequired,
    css: stylesShape
  }

  arrowBtnStyles = {
    width: '25px',
    height: '25px',
    '@media(max-width: 540px)': {
      fontSize: '16px',
      width: '35px',
      height: '35px',
    }
  }

  render () {
    const { onClick, rotation, css, ...otherProps } = this.props

    return (
      <RoundBtn 
        onClick={onClick}
        css={{...this.arrowBtnStyles, ...css}}
        {...otherProps}
      >

      {rotation === 'left' &&
        <FontAwesome
          name="chevron-left"
          style={{
            position: 'relative',
            right: '1px'
          }}
        />
      }

      {rotation === 'right' &&
        <FontAwesome
          name="chevron-right"
          style={{
            position: 'relative',
            left: '1px'
          }}
        />
      }
    </RoundBtn>
    )
  }
}


